import React from "react";
import { Form, Input, Button, message  } from "antd";
import ContactImg from "./contact.jpg";
import './style.css'
import { postContact } from '../../api/contact';

function Contact() {
  const onFinish = async (values) => {
    const body= {
        Email: values.Email,
        CustomerName: values.CustomerName,
        Description: values.PhanHoi,
        Status: false,
    }
    try {
        const respone =  await postContact(body);
        console.log('respone :>> ', respone);
        if(respone.status===200){
            message.success('Gi phn hi thnh cng !');
        }
    } catch (error) {
        message.error(' xy ra li, vui lng th li sau');
        console.log('error :>> ', error);        
    }
  };

  return (
    <>
      <div className="container my-5">
        <div className="row">
          <h3 className="text-info text-center pt-5">LIN H VI CHNG TI</h3>
          <div className="col-5">
            <img src={ContactImg} alt="Lin h" className="img-fluid" />
          </div>
          <div className="col-7 pt-5">
            <div className="form-contact px-5">
              <Form
                layout='vertical'
                initialValues={{
                  remember: true,
                }}
                onFinish={onFinish}
              >
                <Form.Item
                    label='Email ca bn:'
                
                  name="Email"
                  rules={[
                    {
                      type: 'email',
                      message: 'Vui lng nhp ng nh dng E-mail!',
                    },
                    {
                      required: true,
                      message: 'Vui lng khng b trng E-mail!',
                    },
                  ]}
                >
                  <Input placeholder="example@gmail.com"/>
                </Form.Item>
                <Form.Item
                    label='H v tn:'
                
                  name="CustomerName"
                  rules={[
                    {
                      required: true,
                      message: 'Vui lng khng b trng!',
                    },
                  ]}
                >
                  <Input placeholder="H v tn"/>
                </Form.Item>
                <Form.Item
                    label='Phn hi /  kin:'
                  name="PhanHoi"
                  rules={[
                    {
                      required: true,
                      message: "Vui Lng nhp phn hi vo",
                    },
                  ]}
                >
                  <Input.TextArea placeholder="Phn hi c vit ti y..." rows={4}/>
                </Form.Item>

                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                  >
                    Gi
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </div>
        </div>
        <div className="row d-flex justify-content-center" id="tuyen-dung">
            <div className="col-6 text-center">
                <h3 className="text-info text-center pt-5">Tuyn dng</h3>
                <p>
                    Nh tuyn dng: The Laptop World Team
                </p>
                <p>
                    Email lin h: <span className="text-secondary">khainttps12314@fpt.edu.vn</span>
                </p>
                <p>
                    S in thoi lin h: <span className="text-secondary">0909.888.999</span>
                </p>
                <p>
                    a ch: <span className="text-secondary">999, Trng Chinh, G Vp, Thnh ph H Ch Minh</span>
                </p>
            </div>
        </div>

      </div>
    </>
  );
}

export default Contact;
